@import url("https://fonts.googleapis.com/css2?family=Inter+Tight:wght@700;800;900&family=Montserrat:wght@300;400;500;600;700;800;900&display=swap");

*, *::before, *::after{
    margin: 0;
    padding: 0;
    box-sizing: border-box;
    font-family: 'Montserrat','Inter Tight', sans-serif;
    scroll-behavior: smooth;
}

// scroll animation
.reveal {
    opacity: 0;
    transform: translateX(-30px);
    transition: all .1s .2s ease-in-out;
}

.reveal1{
    opacity: 0;
    transform: translateY(-30px);
    transition: all .1s .2s ease-in-out;
}

.reveal2{
    opacity: 0;
    transform: translateY(30px);
    transition: all .1s .2s ease-in-out;
}

.active {
    opacity: 1;
    transform: none;
}

.flex{
    display: flex;
    align-items: center;
}
.icon{
    font-size: 25px;
    font-weight: 100;
}
.container{
    margin: 0 auto;
    max-width: 1300px;
    width: 100%;
}
