@use "../ColorVariables/colors" as *;

.home-container {
  .container1 {
    display: flex;
    justify-content: space-between;
    align-items: flex-end;

    .text-content {
      width: 80%;
      animation: textAnimation 0.3s 0.1s ease-in-out forwards;
      @keyframes textAnimation {
        0% {
          transform: translateX(-10%);
          opacity: 0;
        }
        100% {
          transform: none;
          opacity: 1;
        }
      }
      h1 {
        color: var(--colorThemeOne1, $colorThemeOne1);
        &::before {
          content: "Hi, my name is \A";
          color: black;
        }
        &::after {
          content: "\A Web Developer";
          white-space: pre;
          color: black;
        }
      }
      p {
        font-size: 18px;
        width: 550px;
        margin-top: 10px;
      }
      button {
        background: var(--colorThemeOne3, $colorThemeOne3);
        color: white;
        padding: 15px 20px 15px 20px;
        font-size: 20px;
        border: none;
        border-radius: 6px;
        width: 300px;
        cursor: pointer;
        transition: transform 0.1s ease-in-out;

        &:hover {
          transform: scale(1.02);
        }
      }
    }
    .image-container1 {
      display: flex;
      align-items: flex-end;
      width: 20%;
      position: relative;
      z-index: -1;

      img {
        width: 550px;
        position: absolute;
        animation: imgAnimation 0.3s 0.1s ease-in-out forwards;
        @keyframes imgAnimation {
          0% {
            transform: translateY(10%);
            opacity: 0;
          }
          100% {
            transform: none;
            opacity: 1;
          }
        }
      }
    }
  }
}

@media only screen and (min-width: 200px) and (max-width: 1280px) {
  .home-container {
    padding: 0 18px;
    .container1 {
      flex-direction: column-reverse;
      .text-content {
        margin-top: 50px;
        width: 100%;
        height: 380px;
        p {
          width: 100%;
          font-size: 16px;
        }

        h1 {
          font-size: 30px;
          &::before {
            white-space: pre;
          }
        }
        button {
          width: 100%;
          margin: 50px 0 120px 0;
        }
      }
      .image-container1 {
        background-color: $colorThemeOne4;
        margin-top: 100px;
        overflow: hidden;
        width: 100%;
        height: 200px;
        border-radius: 10px;
        box-shadow: 2px 2px 10px -5px black;
        img {
          width: 220px;
          top: 10px;
          right: 0;
        }
      }
    }
  }
}
@media only screen and (min-width: 500px) {
  .home-container {
    .container1 {
      .text-content {
        button {
          margin: 50px 0 120px 0;
          width: 300px;
        }
      }
      .image-container1 {
        height: 200px;

        img {
          width: 350px;
        }
      }
    }
  }
}
@media only screen and (min-width: 530px) {
  .home-container {
    .container1 {
      .text-content {
        h1 {
          &::before {
            content: "Hi, my name is ";
          }
        }
      }
    }
  }
}
@media only screen and (min-width: 600px) {
  .home-container {
    .container1 {
      .text-content {
        p {
          width: 550px;
        }
      }
    }
  }
}
@media only screen and (min-width: 700px) {
  .home-container {
    padding: 0 40px;
    .container1 {
      .text-content {
        h1 {
          font-size: 35px;
        }
      }
      .image-container1 {
        height: 250px;

        img {
          width: 400px;
        }
      }
    }
  }
}
@media only screen and (min-width: 1000px) and (max-width: 1280px) {
  .home-container {
    .container1 {
      .text-content {
        h1 {
          font-size: 45px;
        }
      }
      .image-container1 {
        height: 370px;

        img {
          width: 570px;
        }
      }
    }
  }
}
@media only screen and (min-width: 1281px) {
  .home-container {
    .container1 {
      padding-top: 240px;

      .text-content {
        height: 500px;
        h1 {
          font-size: 45px;
        }
        button {
          margin-top: 20px;
        }
      }

      .image-container1 {
        height: 0;
        img {
          width: 550px;
          position: absolute;
          left: -110%;
          bottom: 0;
          animation: imgAnimation 0.3s 0.1s ease-in-out forwards;
          @keyframes imgAnimation {
            0% {
              transform: translateY(10%);
              opacity: 0;
            }
            100% {
              transform: none;
              opacity: 1;
            }
          }
        }
      }
    }
  }
}

.home-container-dark-mode {
  background-color: $colorBackground;
  border-bottom: 1px solid rgba(250, 250, 250, 0.2);

  .container1 {
    .text-content {
      h1 {
        &::before {
          color: $colorText;
        }
        &::after {
          color: $colorText;
        }
      }

      p {
        color: $colorText;
      }

      button {
        color: white;
      }
    }

    .image-container1 {
      background: $colorBackground2;
      z-index: 1;
      box-shadow: 0px 0px 1px white;
    }
  }
}
